import axios from "axios";

export function getLogbookDetails(documentId) {

    const config = {
      headers: {
        "Content-type": "application/json",
      },
      withCredentials: true,
    };
  
    return axios.get(`https://vvce-backend.onrender.com/api/v1/logbook/${documentId}`, config);
  }
    
  